import PresentationStep2 from "@/components/presentation-step2";
import Wechat from "@/components/wechat";
import classNames from "classnames";
import styles from "./step-3.module.css";
import Head from "next/head";
import { base_url } from "@/utils/const";
import { useEffect, useState } from "react";
import IOSStep from "@/components/ios-step";
const prevUrl = base_url + "/ios/step-2/";
const nextUrl = "https://www.kadaza.com/";
const moreUrl = "https://immersive-translate.owenyoung.com/usage";

export default function IOSSafari() {
  const translated = useTranslate();
  return (
    <main>
      <Head>
        <title>沉浸式翻译 - 开始使用</title>
        <meta name="viewport" content={"width=device-width"} />
      </Head>
      <div className={classNames("card mt-2")}>
        <IOSStep step={3} />
        <div className={classNames("notranslate")}>
          🎉 恭喜，安装成功！
        </div>
        <PresentationStep2 browser="ios" />
        <div className={classNames("notranslate", styles.footer, "pb-4")}>
          <div>
            <a
              target="_blank"
              rel="noreferrer"
              href={translated ? nextUrl : "javascript:void(0);"}
              className={classNames("ghost", { "disabled": !translated })}
            >
              {translated
                ? "🎉 翻译成功！进入外文网站推荐"
                : "请先点击翻译按钮"}
            </a>
            <link rel="prefetch" href={nextUrl} />
          </div>
        </div>
        <a
          className={classNames("secondary", "ml-4")}
          target="_blank"
          rel="noreferrer"
          href={moreUrl}
        >
          点击了解更多功能
        </a>

        <link rel="prefetch" href={moreUrl} />
      </div>
      <Wechat />
    </main>
  );
}

function useTranslate() {
  const [translated, setTransalted] = useState(false);
  useEffect(() => {
    const listenKey = "immersiveTranslateDocumentMessageTellThirdParty";
    const listener = (e: any) => {
      const { detail } = e;
      if (detail) {
        try {
          const detailObj = JSON.parse(detail);
          if (detailObj.type === "paragraphTranslated") {
            const { ok } = detailObj.payload;
            setTransalted(ok);
          } else {
          }
        } catch (e) {
          console.error(e);
        }
      }
    };
    document.addEventListener(listenKey, listener);
    return () => {
      document.removeEventListener(listenKey, listener);
    };
  }, []);
  return translated;
}
